/* Dark mode (DEFAULT(Cause its better))*/
:root {
	--primary: #2d3b5b;
	--secondary: #b46766;
	--tertiary: #7a698b;
	--lightBackground: #333636;
	--background: #1c1c1e;
	--darkBackground: #070707;
	--text: #fffdf7;
	--darkText: #17161b;
	--blackImgFilter: invert(97%) sepia(97%) saturate(12%) hue-rotate(197deg)
		brightness(105%) contrast(104%);
	--whiteImgFilter: ;
}
/* Light mode */
[data-theme="light"] {
	--primary: #284b63;
	--darkBlue: #0b4f6c;
	--secondary: #854b4c;
	--darkRed: #8d4043;
	--lightBackground: #c4e9d0;
	--background: #d7d1cf;
	--darkBackground: #353535;
	--text: #000000;
	--darkText: #ffffff;
	--blackImgFilter: ;
	--whiteImgFilter: invert(97%) sepia(97%) saturate(12%) hue-rotate(197deg)
		brightness(105%) contrast(104%);
}

html {
	font-size: calc(15px + 0.5vw);
	color: var(--text);
	background-color: var(--background);
}

body {
	font-family: "Montserrat", sans-serif;
	background-color: var(--background);
}

a {
	color: var(--text);
	text-decoration: none;
}

a:hover {
	color: var(--secondary);
}

.bigMe {
	align-self: center;
	width: 35%;
	max-width: 300px;
	height: auto;
	border-radius: 50%;
	padding: 1.5rem;
}

.hero {
	display: flex;
	flex-direction: row;
	width: 100%;
	flex-wrap: wrap;
	justify-content: stretch;
	align-items: stretch;
}
.heroLeft {
	flex-grow: 1;
	min-width: 600px;
	width: 60%;
	height: auto;
	background-color: var(--primary);
	color: white;
	display: flex;
	justify-content: space-evenly;
	flex-direction: row;
}
.heroRight {
	flex-grow: 1;
	min-width: 400px;
	width: 35%;
	height: min-content;
	background-color: var(--background);
	color: var(--text);
}
.heroRight li {
	padding: 0.5rem 0;
}
@media screen and (max-width: 600px) {
	.heroLeft {
		text-align: center;
		flex-wrap: wrap;
		min-width: 0;
	}
	.bigMe {
		width: 40%;
	}
	.heroRight {
		min-width: 0;
		text-align: center;
	}
}

.heroText {
	font-size: 1rem;
	align-self: center;
	padding: 0.5rem 1rem;
}

.nav {
	font-family: "Sono", sans-serif;
	background-color: var(--lightBackground);
	height: auto;
	padding: 1rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 0.5rem + 1vw;
	flex-wrap: wrap;
}
/*justify center if mobile*/
@media only screen and (max-width: 500px) {
	.nav {
		justify-content: center;
	}
}
.navLinks {
	flex-wrap: nowrap;
	flex-shrink: 1;
}

button {
	background-color: transparent;
	border: none;
	color: var(--darkBlue);
	font-size: 2rem;
	padding: 0;
	margin: 0;
}

.navLogo {
	height: 2rem;
	width: auto;
	/* if darkmode invert colors */
	filter: var(--blackImgFilter);
}

.navButtons {
	padding-top: 0.5rem;
}

.navLogo:hover {
	/* for --secondary */
	filter: invert(28%) sepia(78%) saturate(1346%) hue-rotate(309deg)
		brightness(93%) contrast(95%);
	/* for --primary */
	/* filter: invert(30%) sepia(53%) saturate(509%) hue-rotate(182deg)
		brightness(84%) contrast(84%); */
	cursor: pointer;
}
.navLinks a {
	padding: 0 2vw;
}
.navButtons img {
	padding: 0 2vw;
}

.showcase {
	padding: 1rem;
	height: 17rem;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-items: stretch;
}
.projects {
	color: white;
	background-color: var(--secondary);
	text-align: center;
	padding-top: 1rem;
}

.showcaseItem {
	filter: grayscale(100%);
	transition: all 0.5s ease;
	flex-shrink: 1;
	flex-grow: 1;
	width: 100%;
	height: auto;
	background-color: var(--lightBackground);
	color: white;
	margin: 0.5rem;
	padding: 1rem;
	border-radius: 4px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.showcaseItem a {
	color: white;
}
.showcaseItem a:hover {
	color: var(--secondary);
}

.showcaseItem:hover {
	width: 200%;
	background-color: var(--primary);
}

@media screen and (max-width: 600px) {
	.showcase {
		height: auto;
		flex-direction: column;
		align-items: center;
	}
	.showcaseItem {
		width: 65%;
	}
	.showcaseItem:hover {
		width: 90%;
		height: 200%;
	}
}
